.box {
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.6)),
    url("./assets/images/maxresdefault.jpg");
  background-position: center;
  background-size: cover;
  color: #fff;
  display: flex;
}

.left-side {
  width: 70%;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  margin-bottom: 35px;
}

.right-side {
  width: 30%;
  border-left: 2px solid rgb(112, 115, 153);
  background: linear-gradient(to bottom right, #171c61, rgba(86, 90, 153));
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.helps_responsive {
  display: none;
}
.question_responsive {
  display: none;
}

@media screen and (max-width: 768px) {
  .box {
    position: relative;
  }
  .right-side {
    display: none;
  }
  .left-side {
    width: 100vw;
    margin-bottom: 10px;
    border: 1px solid red;
  }
  .helps_responsive {
    display: block;
  }

  .question_responsive {
    display: block;
  }
}
