.list-item,
.list-active {
  width: 70%;
  padding: 0.3em;
  display: flex;
  justify-content: space-around;
  color: rgb(173, 101, 33);
  border-radius: 1.5em;
}

.list-active {
  background-color: rgb(237, 129, 28);
  color: black;
  font-weight: bolder;
}

.item-number {
  align-content: flex-start;
  padding-left: 0.5em;
  font-size: 1.3rem;
  width: 30%;
}

.item-amount {
  width: 70%;
  text-align: left;
  font-size: 1.3rem;
  font-weight: 300;
}
